





















































































































































































































































.movie-info {
  padding: 22px 30px;
  text-align: center;
  border-bottom: 1px solid #e6e6e6;
}

.grade {
  color: #ffb400;
  font-size: 32px;
}

.small {
  font-size: 20px;
}

.movie-desc {
  height: 37px;
  margin-top: 2px;
  line-height: 37px;
  font-size: 26px;
  color: #999;
}

.no-seat {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 460px;
  span {
    margin-top: 24px;
    line-height: 1;
    font-size: 32px;
    color: #acacac;
  }
  img {
    width: 155px;
    height: 143px;
  }
}
.item {
  display: flex;
  padding: 15px 30px 20px 0;
  margin: 0 0 0 30px;
  border-bottom: 1px solid #e6e6e6;
  color: #333;
  &:last-child{
    border: none;
  }
}

.box {
  display: flex;
  height: 70px;
  flex-direction: column;
  justify-content: space-between;
}

.begin {
  line-height: 1;
  font-size: 40px;
  color: #333;
  white-space: nowrap;
}

.end {
  margin-left: 1px;
  color: #999;
  font-size: 22px;
  white-space: nowrap;
}

.info-block {
  margin-left: 34px;
  width: 180px;
  overflow-x: hidden;
}

.lan {
  margin-top: 1px;
  font-size: 26px;
  color: #333;
}

.hall {
  font-size: 22px;
  color: #999;
}

.price {
  width: 260px;
  margin-left: 20px;
}

.price-box {
  display: flex;
  height: 40px;
  align-items: flex-end;
}

.sellPr {
  line-height: 1;
  color: #f03d37;
  margin-top: 1px;
  font-size: 38px;
  margin-right: 10px;
}

.d {
  font-size: 22px;
}

.vipPrice {
  margin-top: 1px;
  border: 1px solid #ff9000;
  border-radius: 2px;
  font-size: 22px;
  .icon {
    display: inline-block;
    font-size: 22px;
    padding: 0 2px;
    color: #fff;
    background-color: #f90;
  }
  .num {
    display: inline-block;
    font-size: 22px;
    padding: 0 2px;
    color: #f90;
    background-color: #fff;
  }
}

.discount {
  color: #999;
  font-size: 22px;
}

.button-block {
  display: flex;
  align-items: center;
  margin-left: 34px;
}

.button {
  width: 90px;
  height: 54px;
  line-height: 54px;
  text-align: center;
  box-sizing: border-box;
  background-color: #f03d37;
  color: #fff;
  border-radius: 8px;
  white-space: nowrap;
  font-size: 24px;
  border: none;
}

.tuan-list {
  padding-left: 30px;
  border-top: 5px solid #f5f5f5;
  margin-top: 30px;
  .title {
    height: 90px;
    line-height: 90px;
    font-size: 30px;
    color: #999;
  }
}

.snack-item {
  display: flex;
  width: 100%;
  box-sizing: border-box;
  border-top: 1px solid #e6e6e6;
  padding: 26px 30px 26px 0;
  img {
    width: 160px;
    height: 160px;
    margin-right: 20px;
  }
}

.snack-info {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  flex-grow: 1;
  overflow: hidden;
}

.first-title {
  font-size: 30px;
  color: #666;
}

.second-title {
  width: 90%;
  font-size: 24px;
  color: #999;
}

.snack-price {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  color: #999;
}

.num {
  font-size: 30px;
  color: #f03d37;
}

.rmb {
  color: #f03d37;
}

.font {
  font-size: 24px;
}
