

































































































.swiper-container {
  position: relative;
  width: 100%;
  height: 350px;
  display: inline-block;
  .post-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    overflow: hidden;
    filter: blur(12px);
    background-position-y: 40%;
  }
  .post-bg-mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #40454d;
    opacity: 0.55;
  }
}

.scroll-view_H {
  flex-wrap: nowrap;
  width: 100%;
  overflow: hidden;
}

.movie-item {
  display: inline-block;
  width: 156px;
  margin: 50px 0 40px 40px;
  transition: all 0.6s;
  &:first-child {
    margin-left: calc(50vw - 78px);
  }
  &:last-child {
    margin-right: calc(50vw - 78px);
  }
  .post {
    width: 156px;
    height: 228px;
    transition: transform 0.3s;
  }
  > .select {
    position: relative;
    transform: scale(1.15);
    border: 2px solid #fff;
    &::after {
      content: "";
      position: absolute;
      bottom: -14px;
      left: 50%;
      transform: translateX(-50%);
      width: 20px;
      height: 10px;
      background-image: url("data:image/png;base64,");
      background-repeat: no-repeat;
      background-size: contain;
    }
  }
  img {
    width: 100%;
    height: 100%;
  }
}
